<template>
    <div class="c-facedat-upload">
        <div class="u-upload" v-if="support">
            <template v-if="!done">
                <input class="u-input" type="file" id="face_file" accept=".dat,.jx3dat,.ini" @change="uploadData" />
                <el-button class="u-btn" type="primary" @click="selectData" icon="el-icon-upload2">{{
                    btnText
                }}</el-button>
                <slot name="guide">
                    <a v-if="type === 'face'" class="u-help" href="/tool/746" target="_blank">
                        <i class="el-icon-collection"></i> 
                    </a>
                </slot>
            </template>
            <template v-else>
                <div class="u-result">
                    <el-button class="u-reset" @click="resetData" icon="el-icon-refresh-left"></el-button>
                    <el-alert :title="'' + filename" type="success" show-icon></el-alert>
                </div>
            </template>
        </div>
        <el-alert
            v-else
            class="u-notsupport"
            title=""
            type="warning"
            description=",chrome"
            show-icon
        ></el-alert>
    </div>
</template>

<script>
import types from "../assets/data/index.json";
import { load } from "./DataRouter";

export default {
    name: "Upload",
    props: {
        type: {
            type: String,
            default: "",
        },
    },
    components: {},
    data: function () {
        return {
            support: true,

            parsedType: "", //
            file: "", //
            json: "", //json data
            object: "", //js object

            done: false,
            types: types.bodyMap,

            fileTypeNames: {
                face: "",
                face_v2: "",
                face_ini: "",
                body: "",
            },
        };
    },
    computed: {
        btnText() {
            let txt = "/";
            if (this.type === "face") {
                txt = "";
            }
            if (this.type === "body") {
                txt = "";
            }
            return txt;
        },
        filename: function () {
            return this.file && this.file.name;
        },
    },
    methods: {
        // 
        selectData: function (i) {
            let fileInput = document.getElementById("face_file");
            fileInput.dispatchEvent(new MouseEvent("click"));
        },
        uploadData: function (e) {
            let file = e.target.files[0];
            this.file = file;
            this.parseData(file);
        },
        // 
        parseData: function (file) {
            if (!file) return; //
            const vm = this;

            //  File  ArrayBuffer 
            let fr = new FileReader();
            fr.onload = function (e) {
                console.log("......");
                const result = load(e.target.result);
                if (!result) {
                    return vm.$notify.error({
                        title: "",
                        message: "",
                    });
                }

                if (result.type.startsWith("face") && vm.type && vm.type !== "face") {
                    return vm.$notify.error({
                        title: "",
                        message: "",
                    });
                }
                if (result.type === "body" && vm.type && vm.type !== "body") {
                    return vm.$notify.error({
                        title: "",
                        message: "",
                    });
                }

                vm.parsedType = result.type;
                vm.object = result.data;

                // 
                if (vm.object) {
                    try {
                        vm.json = JSON.stringify(vm.object);
                        console.log();
                        vm.$notify({
                            title: "",
                            message: `${vm.types[vm.object.nRoleType.toString()]?.label || ""}${
                                vm.fileTypeNames[vm.parsedType]
                            }`,
                            type: "success",
                        });
                        vm.done = true;
                        vm.$emit("success", {
                            type: vm.parsedType,
                            file: vm.file,
                            json: vm.json,
                            object: vm.object,
                        });
                    } catch (e) {
                        console.log(e);
                        vm.$notify.error({
                            title: "",
                            message: "",
                        });
                        vm.$emit("fail", {
                            file: vm.file,
                        });
                    }
                }
            };
            fr.onerror = function (e) {
                vm.$notify.error({
                    title: "",
                    message: "",
                });
            };
            fr.readAsArrayBuffer(file);
        },
        // 
        resetData: function () {
            this.done = false;

            this.file = "";
            this.json = "";
            this.object = "";
            this.$emit("success", {
                file: "",
                json: "",
                object: "",
            });
        },
    },
    filters: {},
    created: function () {},
    mounted: function () {
        this.support = !!window.FileReader;
    },
};
</script>

<style lang="less">
.c-facedat-upload {
    .u-tip {
        .fz(12px);
        color: #999;
    }
    .mt(20px);
    .u-input {
        .none;
    }
    .u-upload {
        .x;
    }
    .u-help {
        .fz(12px,3);
        color: @color-link;
        .underline(@color-link);
        i {
            .fz(14px);
            .y(-1px);
        }
    }
    .u-btn {
        .db;
        margin: 0 auto;
        .size(200px,50px);
    }
    .u-file {
        .fz(14px,2);
        color: #49c10f;
        .bold;
    }
    .u-reset {
        .mb(10px);
    }
}
</style>
